import React from 'react';
import '../index.css';
import Icon from "@material-ui/core/Icon";
import Paper from '@material-ui/core/Paper';

export default class SetupPin extends React.Component {
    state = {
        pwd : '',
    };

    passTyped = (ev) => {
        this.setState({pwd: ev.target.value});
        this.props.appSetState({isSetupPassword: ev.target.value});
    };

    numbClick(numb) {
        let pwd = this.state.pwd+''+numb;
        this.setState({pwd: pwd});
        this.props.appSetState({isSetupPassword: pwd });
    };


    render() {
        if (!this.props.app.isSetup || this.props.app.isSetupLogged) return null;

        return (
            <div className="app-page-login p-2 w-100 h-100">
                <div className="app-login-wrapper-bkg app-bg-e0 app-page-start p-2 w-100 h-100"> </div>

                <div className="app-main app-login-wrapper w-100">
                    <Paper className="app-login p-2">
                        <form onSubmit={(ev)=>{this.setState({pwd: ''}); this.props.Login(ev)}}>
                            <div className="form-group">
                                <label htmlFor="p">Pin</label>
                                <input id="p" type="text" autoComplete={'off'} className={"app-login-pass form-control text-center"} value={this.state.pwd} onChange={this.passTyped} autoFocus/>
                            </div>
                            <div className="numbrow">
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("7")}
                                            type="button" > 7
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("8")}
                                            type="button" > 8
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("9")}
                                            type="button" > 9
                                    </button>
                                </div>
                            </div>

                            <div className="numbrow">
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("4")}
                                            type="button" > 4
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("5")}
                                            type="button" > 5
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("6")}
                                            type="button" > 6
                                    </button>
                                </div>
                            </div>

                            <div className="numbrow">
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("1")}
                                            type="button" > 1
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("2")}
                                            type="button" > 2
                                    </button>
                                </div>
                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("3")}
                                            type="button" > 3
                                    </button>
                                </div>
                            </div>
                            <div className="numbrow">
                                <div className="m-2 d-inline-block">
                                    <button className={"btn btn-success btn-medium btn-3d"}
                                            disabled={false}
                                            title="Login"
                                            type="submit" >
                                        <Icon>vpn_key</Icon>
                                    </button>
                                </div>

                                <div className="m-2 d-inline-block">
                                    <button className="btn btn-medium btn-3d"
                                            disabled={false}
                                            onClick={()=>this.numbClick("0")}
                                            type="button" > 0
                                    </button>
                                </div>

                                <div className="m-2 d-inline-block">
                                    <button className={"btn btn-medium btn-3d"}
                                            onClick={()=>{this.setState({pwd: ''});this.props.Logout()}}
                                            disabled={false}
                                            title="Logout"
                                            type="button" >
                                        <Icon>close</Icon>
                                    </button>
                                </div>

                            </div>

                        </form>
                    </Paper>

                </div>

            </div>

        )
    }
}

