import React from "react";
import * as tools from "../../tools";
import Icon from "@material-ui/core/Icon";
import Fab from '@material-ui/core/Fab';
import ImageEditorZoom from "./ImageEditorZoom";
import ImageEditorBrightness from "./ImageEditorBrightness";
import ImageEditorColor from "./ImageEditorColor";

export default class ImageEditor extends React.Component {
    state = {
        showTabButton: true,
    };


    componentDidUpdate(prevProps, prevState, snapshot) {
        if(!this.props.app.images[this.props.app.currentImage].editorState.initedMask) {
            this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas:true, currentTab:0} );
        }
    }

    tabChange = (value) => {
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas:true, currentTab:value} );
    };

    // setStateFromChild=(newState, callback)=> {
    //     // console.log(newState);
    //     this.setState(newState, ()=>{
    //         if(typeof(callback)==='function') callback();
    //     });
    // };


    alignTop = ()=> {
        let image = this.props.app.images[this.props.app.currentImage];
        // if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) return;
        let sliderPositionTB = 0;
        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
            sliderPositionTB = (image.editorState.imgWidth-image.editorState.imgHeight)/2;
        }
        this.props.appImageChanged( this.props.app.currentImage, {sliderPositionTB:sliderPositionTB} );
    };

    alignRight = ()=> {
        let image = this.props.app.images[this.props.app.currentImage];
        // if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) return;

        let imgWidth = tools.dpi_inch2dots(image.width, image.format.dpi)*image.editorState.imageZoom*image.editorState.ratio;
        let sliderPositionLR = this.props.app.editorWidth - image.editorState.maskLR*2 - imgWidth;

        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
            sliderPositionLR += (image.editorState.imgWidth-image.editorState.imgHeight)/2;
        }
        this.props.appImageChanged( this.props.app.currentImage, {sliderPositionLR: sliderPositionLR} );

    };

    alignBottom = ()=> {
        let image = this.props.app.images[this.props.app.currentImage];
        // if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) return;
        let imgHeight = tools.dpi_inch2dots(image.height, image.format.dpi)*image.editorState.imageZoom*image.editorState.ratio;
        let sliderPositionTB =  this.props.app.editorHeight - image.editorState.maskTB*2 - imgHeight;
        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
            sliderPositionTB -= (image.editorState.imgWidth-image.editorState.imgHeight)/2;
        }

        this.props.appImageChanged( this.props.app.currentImage, {sliderPositionTB:sliderPositionTB} );
    };

    alignLeft = ()=> {
        let image = this.props.app.images[this.props.app.currentImage];
        // if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) return;
        let sliderPositionLR = 0;
        if(image.editorState.imageRotate===90 || image.editorState.imageRotate===270) {
            sliderPositionLR -= (image.editorState.imgWidth-image.editorState.imgHeight)/2;
        }

        this.props.appImageChanged( this.props.app.currentImage, {sliderPositionLR:sliderPositionLR} );
    };

    render() {

        const image = this.props.app.images[this.props.app.currentImage];
        let currentTab = image.editorState.currentTab;
        if(currentTab<0 || currentTab>2) currentTab = 0;

        return (
            <div className="div-h-100 position-relative p-2" ref={(node)=>{this.editorWrapper=node}}>

                {currentTab===0 &&(
                    <ImageEditorZoom
                        {...this.props}
                        alignTop = {this.alignTop.bind(this)}
                        alignRight = {this.alignRight.bind(this)}
                        alignBottom = {this.alignBottom.bind(this)}
                        alignLeft = {this.alignLeft.bind(this)}
                    />
                )}
                {currentTab===1 &&(
                    <ImageEditorBrightness
                        {...this.props}
                    />
                )}
                {currentTab===2 &&(
                    <ImageEditorColor
                        {...this.props}
                    />
                )}



                    <div onClick={ ()=>{this.setState({showTabButton: !this.state.showTabButton})} }
                        className="app-btn-pie app-main-editor-tabs-pie bg-primary text-light ">
                        <Icon>settings_brightness</Icon>
                    </div>

                    {this.state.showTabButton &&(
                        <Fab color="primary" className={'app-main-editor-tabs-pie-button position-absolute bg-success'}
                             style={{right: '1em', bottom: '8em'}}
                             onClick={()=>{this.tabChange(2)}}
                             disabled={false}
                             aria-label="Colors">
                            <Icon>tune</Icon>
                        </Fab>

                    )}

                    {this.state.showTabButton &&(
                        <Fab color="primary" className={'app-main-editor-tabs-pie-button position-absolute bg-success'}
                             style={{right: '5.5em', bottom: '5.5em' }}
                             onClick={()=>{this.tabChange(1)}}
                             disabled={false}
                             aria-label="Brightness"
                             type="button" >
                            <Icon>brightness_medium</Icon>
                        </Fab>
                    )}

                    {this.state.showTabButton &&(
                        <Fab color="primary" className={'app-main-editor-tabs-pie-button position-absolute bg-success'}
                             style={{right: '8em', bottom: '1em' }}
                             onClick={()=>{this.tabChange(0)}}
                             disabled={false}
                             aria-label="Zoom">
                            <Icon>transform</Icon>
                        </Fab>
                    )}

                    {this.state.showTabButton &&(
                        <Fab color="primary" className={'app-main-editor-tabs-pie-button position-absolute bg-success'}
                             style={{right: '1em', bottom: '18em' }}
                             onClick={()=>{this.props.changeCountImage(this.props.app.currentImage, image.count+1)}}
                             disabled={image.count>98}
                             aria-label="Increase">
                            <Icon>add</Icon>
                        </Fab>
                    )}

                    {this.state.showTabButton &&(
                        <Fab color="primary" className={'app-main-editor-tabs-pie-button position-absolute bg-success'}
                             style={{right: '1em', bottom: '13em' }}
                             onClick={()=>{this.props.changeCountImage(this.props.app.currentImage, image.count-1)}}
                             disabled={image.count<0}
                             aria-label="Decrease">
                            <Icon>remove</Icon>
                        </Fab>
                    )}







            </div>

        )
    }
}

