import React from 'react';

import './index.css';

import t from "../i18n";
import * as tools from "../../tools";


import ImageThumb from "./ImageThumb"
import ImageEditor from "./ImageEditor"

import Paper from "@material-ui/core/Paper";
import Icon from "@material-ui/core/Icon";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Fab from "@material-ui/core/Fab";
import ClickAwayListener from '@material-ui/core/ClickAwayListener';


export default class PageEditor extends React.Component {
    constructor(props) {
        super(props);
        this.undo = {
            history:[],
            current:null,
            skipChanges: true,
        };


        this.state = {
            confirmBack: false,
            imagesCount: 0,
            imagesSum: 0,
        };
    }

    componentDidMount() {
        // if(this.props.app.currentImage===null)
            this.selectImage(0);
        this.countSumm();
    };

    countSumm() {
        let imagesCount = 0;
        let imagesSum = 0;
        let len=this.props.app.images.length;
        for(let i=0; i<len; i++) {
            imagesCount += this.props.app.images[i].count;
            imagesSum += this.props.app.images[i].count * this.props.app.images[i].format.cost;
        }
        this.setState({imagesCount:imagesCount, imagesSum:imagesSum})
    }

    shouldComponentUpdate(nextProps, nextState, nextContext) {
        if(this.props.app.images[this.props.app.currentImage].editorState.initedImagePosition && this.props.app.images[this.props.app.currentImage].editorState.initedMask) {
            let wasChanged = true;
            if(this.undo.history.length>0) {
                let lastUndo = this.undo.history[this.undo.history.length-1];
                wasChanged = !(tools.deepCompare(lastUndo, this.props.app.images[this.props.app.currentImage].editorState));
            }
            // console.log('skipChanges:', this.undo.skipChanges, 'history.length', this.undo.history.length);
            if(!this.undo.skipChanges || this.undo.history.length===0) {
                // console.log('wasChanged:', wasChanged);
                if(wasChanged) {
                    if(this.undo.current!==null) {
                        this.undo.history = this.undo.history.slice(0, this.undo.current-1);
                        // console.log('this.undo.history.slice', 0, this.undo.current);
                        // console.log('slice undo', this.undo);
                    }
                    this.undo.history.push( Object.assign({}, this.props.app.images[this.props.app.currentImage].editorState ) );
                    let maxHistoryLength = 99;
                    if(this.undo.history.length>maxHistoryLength) {
                        let start = this.undo.history.length-maxHistoryLength;
                        this.undo.history = this.undo.history.slice(start, start+maxHistoryLength);
                    }
                    this.undo.current =null;
                    // console.log('add undo', this.undo);
                }
            }
            this.undo.skipChanges = false;
        }

        return true;
    }

    componentDidUpdate(prevProps, prevState, snapshot) {

        // if(!this.props.app.images[this.props.app.currentImage].editorState.initedMask) {


        // if(!this.props.app.images[0].editorState.initedMask) {
        //     this.selectImage(0);
        //     return;
        // }
        this.props.savePages();


    }

    checkUndo = () => {
        return !(this.undo.history.length===1 || this.undo.current===0);
    };
    clickUndo = () => {
        if(!this.checkUndo) return false;
        let currentUndo = this.undo.history.length-2;
        if(this.undo.current>0) currentUndo = this.undo.current-1;
        this.props.appImageChanged( this.props.app.currentImage, this.undo.history[currentUndo] );

        this.undo.current = currentUndo;
        this.undo.skipChanges = true;

        // console.log('clickUndo', currentUndo);
        // console.log('undo:', this.undo);

    };

    checkRedo = () => {
        return !(this.undo.current===null || this.undo.current===this.undo.history.length-1);
    };
    clickRedo = () => {
        if(!this.checkRedo) return false;
        let currentUndo = this.undo.current+1;
        this.props.appImageChanged( this.props.app.currentImage, this.undo.history[currentUndo] );

        this.undo.current = currentUndo;
        this.undo.skipChanges = true;

        // console.log('clickRedo', currentUndo);
        // console.log('undo:', this.undo);

    };

    selectImage = (index) => {
        // console.log('selected '+index, this.props.app.currentImage);
        if(index!==this.props.app.currentImage) {
            this.undo = {
                history:[],
                current:null,
                skipChanges: true,
            };
        }

        this.props.appSetState({
            currentImage : index,
        });
    };

    changeCurrentImage = (step) => {
        const newCurrent = this.props.app.currentImage+step;
        if(newCurrent>=0 && newCurrent<this.props.app.images.length) {
            this.selectImage(newCurrent);
        }
    };

    changeCountImage = (index, count) => {
        if(count>-1) {
            let images = this.props.app.images;
            images[index].count = count;
            let state = {images : images};
            if(count===0) {
                this.changeCurrentImage(1);
            }
            this.props.appSetState(state, ()=>{this.countSumm()})
        } else {
            this.changeCurrentImage(1);
            if(this.props.app.currentImage<this.props.app.images.length-1) {
                this.props.appSetState({currentImage : this.props.app.currentImage+1});
            }
        }

    };


    scrollIntoViewIfNeed = (domNode) => {
        domNode.scrollIntoViewIfNeeded();
    };

    backPressed() {
        this.setState({confirmBack: true});

    }
    backConfirmed(back) {
        if(back) {
            this.props.onClickPrevBtn();
        }
        this.setState({confirmBack: false});
    }

    openFormatsPanel = () => {
        this.setState({isFormatsOpen:!this.state.isFormatsOpen});
    };

    formatChange(index) {
        let format = this.props.app.formats[index];
        let currentImage = this.props.app.currentImage;
        let images = this.props.app.images; //[this.props.app.currentImage];
        images[currentImage].format = format;
        images[currentImage].editorState.initedMask = false;
        images[currentImage].editorState.initedImagePosition = false;

        this.props.appSetState({images: images}, ()=>{
            this.props.appImageInitMask(currentImage, true, ()=>{
                this.props.alignInit(currentImage, ()=>{this.countSumm()});
            });
        });

        // appSetState
        this.setState({isFormatsOpen:false});
    }

    repeatColors =() => {
        let colors = this.getColorsToRepeat();
        if(colors)
            this.props.appImageChanged( this.props.app.currentImage, colors );
    };

    getColorsToRepeat = () => {
        if(this.props.app.currentImage===null || this.props.app.currentImage===0) return false;
        let editorState = this.props.app.images[this.props.app.currentImage-1].editorState;
        let current =  this.props.app.images[this.props.app.currentImage].editorState;
        let hasChange = false;
        if(editorState.brightness!==current.brightness) hasChange = true;
        if(editorState.contrast!==current.contrast) hasChange = true;
        if(editorState.saturate!==current.saturate) hasChange = true;
        if(editorState.colorR!==current.colorR) hasChange = true;
        if(editorState.colorG!==current.colorG) hasChange = true;
        if(editorState.colorB!==current.colorB) hasChange = true;

        if(!hasChange) return false;
        return {
            brightness: editorState.brightness,
            contrast: editorState.contrast,
            saturate: editorState.saturate,
            colorR: editorState.colorR,
            colorG: editorState.colorG,
            colorB: editorState.colorB,
        }
    };

    render() {

        let image = this.props.app.images[this.props.app.currentImage];
        let format = image ? image.format : {};
        // let format_lanscape = (format.width-format.height)>1;

        let formatsList = this.state.isFormatsOpen ? (
            <ClickAwayListener onClickAway={()=>{this.openFormatsPanel()}}>
                <Paper className="app-main-change-formats scrollbar position-absolute">
                    <table className="table-formats">
                        {this.props.app.formats.map( (format, index)=>format.enabled ?
                            <tbody key={index} onClick={()=>this.formatChange(index)}>
                            <tr>
                                <th scope="row">{format.name}</th>
                                <th scope="row">{format.height}"x {format.width}"</th>
                                <th scope="row">{Math.round(format.height*25.4)}x{Math.round(format.width*25.4)}</th>
                                <th scope="row">{t(format.surface)}</th>
                                <th scope="row">{tools.Round(format.cost)}</th>
                            </tr>
                            </tbody>

                            : '' )}
                    </table>
                </Paper>
            </ClickAwayListener>
        ) : null;

        let rightwidget = (
            <div className="app-main-rightwidget h-100 p-2 d-inline-block">
                <Paper className="h-100 p-2 app-bg-e position-relative">
                    <div className="_fabwrapper">
                        <button className={"btn btn-big btn-primary"}
                                onClick={() => this.props.onClickNextBtn()}
                                disabled={false}
                                title={t('Next')}
                                type="button" >
                            <Icon>arrow_forward</Icon> {t('Next')}
                        </button>
                    </div>

                    <div className="_fabwrapper">
                        <button className={"btn btn-big btn-primary"}
                                onClick={this.backPressed.bind(this)}
                                disabled={false}
                                title={t('Previous')}
                                type="button" >
                            <Icon>arrow_back</Icon> {t('Previous')}
                        </button>
                    </div>

                    <div className="_fabwrapper">
                        <button className="btn btn-light mr-2"
                                onClick={this.repeatColors.bind(this)}
                                disabled={!this.getColorsToRepeat()}
                                title={t('repeat color settings')}
                                type="button" >
                            <Icon>compare</Icon>
                        </button>

                        <button className="btn btn-light mr-2"
                                onClick={this.clickUndo.bind(this)}
                                disabled={!this.checkUndo()}
                                title={t('Undo')}
                                type="button" >
                            <Icon>undo</Icon>
                        </button>

                        <button className="btn btn-light mr-2"
                                onClick={this.clickRedo.bind(this)}
                                disabled={!this.checkRedo()}
                                title={t('Redo')}
                                type="button" >
                            <Icon>redo</Icon>
                        </button>
                    </div>

                    <div className="app-main-rightwidget-tablewrp">

                        {formatsList}

                        <Fab color="primary" className="app-main-change-formats-button position-absolute text-primary bg-light"
                             style={{right: '0', bottom: '100%' }}
                             onClick={()=>{this.openFormatsPanel()}}
                             aria-label="Colors">
                            <Icon>menu</Icon>
                        </Fab>

                        <table className="table">
                            <tbody>

                            <tr>
                                <th scope="row" colSpan={2}>
                                    {t('Format')} {format.name}
                                </th>
                            </tr>
                            <tr>
                                <th scope="row" colSpan={2}>
                                    {format.height}"x {format.width}" {Math.round(format.height*25.4)}x{Math.round(format.width*25.4)}
                                </th>
                            </tr>

                            <tr>
                                <th scope="row">{t('Files')}</th>
                                <th scope="row">{this.props.app.images.length} / {this.state.imagesCount}</th>
                            </tr>
                            <tr>
                                <th scope="row">{t('Cost')}</th>
                                <th scope="row">{tools.Round(this.state.imagesSum)}</th>
                            </tr>
                            </tbody>
                        </table>

                    </div>

                </Paper>

            </div>
        );
        let dialogConfirmBack = (
            <Dialog
                open={this.state.confirmBack}
                onClose={()=>this.backConfirmed(false)}
                aria-labelledby="alert-dialog-title"
                aria-describedby="alert-dialog-description"
            >
                <DialogTitle id="alert-dialog-title">{t("Warning")}</DialogTitle>
                <DialogContent>
                    <DialogContentText id="alert-dialog-description">
                        {t('Current state of editor images will be lost.')} {t('Are you sure?')}
                    </DialogContentText>
                </DialogContent>
                <DialogActions>
                    <button onClick={()=>this.backConfirmed(true)} className={"btn btn-big btn-light"} autoFocus>
                        {t('OK')}
                    </button>
                    <button onClick={()=>this.backConfirmed(false)} className={"btn btn-big btn-primary"}>
                        {t('Cancel')}
                    </button>
                </DialogActions>
            </Dialog>

        );

        return (

                <div className="app-page app-bg-e0">
                    <div style={{display:'none', visibility:'hidden'}}>
                        <canvas ref={(node)=>{this.processCanvasNode=node}} />
                    </div>

                    <div className="workplace">
                        <div className="vh100 p-2">
                            <Paper className="app-editor-items-scroller p-2 app-bg-e">
                                <div className="app-editor-items-scroller__button">
                                    <button type="button"
                                            className={"btn btn-primary"}
                                            disabled={this.props.app.currentImage===0}
                                            onClick={()=>{this.changeCurrentImage(-1)}}>
                                        <Icon>expand_less</Icon>
                                    </button>
                                </div>
                                <div className="scrollbar _vertical">

                                    <div id='listImages' className="app-editor-left-thumblist p-2">
                                        {this.props.app.images.map( (image, index)=>
                                            <ImageThumb
                                                key={index}
                                                image={image}
                                                itemIndex={index}
                                                selected={this.props.app.currentImage===index}
                                                onSelect={()=>this.selectImage(index)}
                                                scrollIntoView={this.scrollIntoViewIfNeed}
                                                {...this.props}
                                            />
                                        )}

                                    </div>

                                </div>

                                <div className="app-editor-items-scroller__button">
                                    <button type="button"
                                            className={"btn btn-primary"}
                                            disabled={this.props.app.currentImage===this.props.app.images.length-1}
                                            onClick={()=>{this.changeCurrentImage(1)}}>
                                        <Icon>expand_more</Icon>
                                    </button>
                                </div>
                            </Paper>

                        </div>

                        {this.props.app.currentImage!==null && (
                            <ImageEditor
                                changeCountImage={this.changeCountImage.bind(this)}
                                processCanvas={this.processCanvasNode}
                                onMessage={this.props.onMessage}
                                {...this.props}
                            />
                        )}

                        <div className="tools">
                            {rightwidget}
                        </div>
                    </div>
                    {dialogConfirmBack}
                </div>


        )
    }
}





