import React from "react";
import Icon from "@material-ui/core/Icon";
import Slider from "@material-ui/lab/Slider";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Switch from '@material-ui/core/Switch';
import ImageEditorColorCanvas from "./ImageEditorColorCanvas";
import Paper from "@material-ui/core/Paper";
// import {THEME} from "../../tools";


export default class ImageEditorBrightness extends React.Component {

    sliderChangeBrightness =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, brightness: value} );
        // this.props.setStateFromChild({ brightness: value, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderChangeContrast =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, contrast: value} );
        // this.props.setStateFromChild({ contrast: value, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderChangeSaturate =  (event, value)=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, saturate: value} );
        // this.props.setStateFromChild({ saturate: value, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderResetBrightness =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, brightness: 1} );
        // this.props.setStateFromChild({ brightness: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderResetContrast =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, contrast: 1} );
        // this.props.setStateFromChild({ contrast: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderResetSaturate =  ()=>{
        this.props.appImageChanged( this.props.app.currentImage, {needUpdateCanvas: true, saturate: 1} );
        // this.props.setStateFromChild({ saturate: 1, needUpdateCanvas: true }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };
    sliderChangeSepia = () => {
        this.props.appImageChanged(this.props.app.currentImage, {
            sepia: !this.props.app.images[this.props.app.currentImage].editorState.sepia,
            colorR: 1,
            colorG: 1,
            colorB: 1,
            needUpdateCanvas: true,
        } );
        // this.props.setStateFromChild({
        //     sepia: !this.props.parentState.sepia,
        //     colorR: 1,
        //     colorG: 1,
        //     colorB: 1,
        //     needUpdateCanvas: true,
        // }, ()=>{
        //     this.props.setStateFromChild({needUpdateCanvas: false});
        // })
    };


    render() {

        const image = this.props.app.images[this.props.app.currentImage];

        return (
            <Paper className="canvas_wrapper p-2 app-bg-e">
                <div className="canvas_container">
                    <div className="canvas">
                        <div className="img_container">
                            <ImageEditorColorCanvas
                                {...this.props}
                            />
                        </div>
                    </div>
                </div>
                <div className="canvas_actions">
                    <div className="app-main-editor-visual-right-colors h-50">
                        <div className="h-75 d-flex flex-row">
                            <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                                <Icon className={'_reset'} color='primary' onClick={this.sliderResetBrightness}>brightness_medium</Icon>
                                <Slider className='app-slider-for-filter'
                                        max={2}
                                        value={image.editorState.brightness}
                                        min={0}
                                        onChange={this.sliderChangeBrightness}
                                        vertical
                                />
                                {this.props.app.isDebugShow && (
                                    <div className={'_value'}><small>({image.editorState.brightness})</small></div>
                                )}
                            </div>
                            <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                                <Icon className={'_reset'} color='primary' onClick={this.sliderResetContrast}>tonality</Icon>
                                <Slider className='app-slider-for-filter'
                                        max={2}
                                        value={image.editorState.contrast}
                                        min={0}
                                        onChange={this.sliderChangeContrast}
                                        vertical
                                />
                                {this.props.app.isDebugShow && (
                                    <div className={'_value'}><small>({image.editorState.contrast})</small></div>
                                )}
                            </div>
                            <div className="vert-slider-wrapper h-100 position-relative d-inline-flex">
                                <Icon className={'_reset'} color='primary' onClick={this.sliderResetSaturate}>color_lens</Icon>
                                <Slider className='app-slider-for-filter'
                                        max={2}
                                        value={image.editorState.saturate}
                                        min={0}
                                        onChange={this.sliderChangeSaturate}
                                        vertical
                                />
                                {this.props.app.isDebugShow && (
                                    <div className={'_value'}><small>({image.editorState.saturate})</small></div>
                                )}
                            </div>


                        </div>
                        <div className="h-25">
                            <div className="hori-slider-wrapper w-100 position-relative d-inline-flex">
                                <FormControlLabel
                                    control={
                                        <Switch
                                            checked={image.editorState.sepia}
                                            onChange={this.sliderChangeSepia}
                                            value="true"
                                            color="primary"
                                        />
                                    }
                                    label="Sepia"
                                />
                            </div>

                        </div>


                    </div>


                </div>


            </Paper>


        )

    }
}

